.navbar {
  display: flex;
  justify-content: space-between;
  align-items: center;
  background-color: #283e4a;
  padding: 10px 20px;
}

.navbar-logo h2 {
  color: #fff;
}

.navbar-links a {
  color: #fff;
  margin-right: 15px;
  text-decoration: none;
  font-size: 16px;
}

.navbar-search input {
  padding: 5px;
  border-radius: 4px;
  border: 1px solid #ccc;
}
